@import "../scss/variables";

/*---------- Header-styles ----------*/


/*Header-Color*/

.color-header {
    .main-header-center .form-control {
        color: $white;
        &::placeholder {
            color: $white !important;
            opacity: .3;
        }
    }
    .hor-header .header-brand1 {
        margin-top: 4px;
    }
}

.color-header {
    .main-header-center .form-control::placeholder {
        color: $white !important;
        opacity: .3;
    }
    .app-header,
    .hor-header {
        background: $primary-1;
        border-bottom: 1px solid rgba(255, 255, 255, .1);
    }
    .app-sidebar__toggle:hover {
        color: #fff;
    }
    .demo-icon.nav-link i {
        color: #e7e7f7 !important;
    }
    .animated-arrow {
        background: none;
        span {
            background: #e7e7f7;
            &:before,
            &:after {
                background: #e7e7f7;
            }
        }
    }
    &.active .animated-arrow span {
        background-color: transparent;
    }
    .header {
        .form-inline .form-control {
            border: 1px solid $white-05 !important;
            background: rgba(0, 0, 0, 0.08);
            color: $white;
        }
    }
    .header .form-inline {
        .btn i {
            color: $white !important;
            opacity: .5;
        }
        .form-control::placeholder {
            color: $white !important;
            opacity: .3;
        }
    }
    .app-sidebar__toggle {
        color: #fff;
        a {
            color: $white;
        }
    }
    .hor-header {
        .header-brand-img.light-logo {
            display: block;
        }
    }
    .logo-horizontal .header-brand-img.light-logo1 {
        display: none;
    }
}

@media (min-width: 992px) {
    .color-header {
        .hor-header.header .header-brand .header-brand-img {
            &.desktop-lgo {
                display: none;
            }
            &.dark-logo {
                display: block;
                // margin-top: 5px;
            }
        }
    }
}

@media (min-width: 767px) {
    .color-header {
        .header .nav-link.icon i,
        .header-right .nav-link.icon:hover i {
            color: $white!important;
        }
        .nav-link.icon {
            color: $white!important;
        }
        .nav-link i {
            color: #e7e7f7 !important;
            &:hover {
                color: $white;
            }
        }
    }
}

@media (max-width: 767px) {
    .color-header {
        .hor-header.header .header-brand-img {
            &.mobile-logo {
                display: none !important;
            }
            &.darkmobile-logo {
                display: block;
            }
        }
        .hor-header {
            .header-brand-img {
                &.desktop-logo {
                    display: none;
                }
                &.light-logo {
                    margin: 0 auto;
                    margin-top: 6px;
                }
            }
        }
    }
}

@media (max-width: 1024px) and (min-width: 768px) {
    .color-header {
        .hor-header.header .header-brand-img {
            &.desktop-lgo,
            &.mobile-logo {
                display: none !important;
            }
            &.dark-logo {
                display: block !important;
                // margin-top: 5px;
            }
        }
    }
}

@media (max-width: 767px) {
    .color-header {
        .app-header .header-brand1 .header-brand-img.light-logo1 {
            display: none !important;
        }
        .app-header .header-brand1 .header-brand-img.desktop-logo {
            display: block !important;
        }
        .app-header.header .header-brand-img.desktop-logo{
            display: block;
        }
        .logo-horizontal .header-brand-img.light-logo1{
            display: none !important;
        }
    }
}

.dark-mode.color-header {
    .app-header,
    .hor-header {
        background: $primary-1;
    }
    .header .form-inline .form-control {
        border: 1px solid $white-05 !important;
        color: $white;
    }
}


/*Header-dark*/

.dark-header {
    .app-header,
    .hor-header {
        background: $dark-theme-1 !important;
        border-bottom: 1px solid rgba(255, 255, 255, .1);
    }
    .hor-header .header-brand1 {
        margin-top: 4px;
    }
    .animated-arrow {
        background: none;
        span {
            background: $primary-1;
            &:before,
            &:after {
                background: $primary-1;
            }
        }
    }
    &.active .animated-arrow span {
        background-color: transparent;
    }
    .header.hor-header .nav-link.icon,
    .header-right-icons .nav-link.icon:hover {
        color: #e7e7f7 !important;
    }
    .app-header.header .nav-link:hover {
        color: $white;
    }
    .app-sidebar__toggle:hover {
        color: #fff;
    }
    .header .main-header-center .form-control {
        border: 1px solid $white-1 !important;
        color: $white;
        background: #353554;
    }
    .header .form-inline {
        .btn i {
            color: $white;
            opacity: .5;
        }
        .form-control::placeholder {
            color: $white;
            opacity: .3;
        }
    }
    .header.hor-header .header-brand-img {
        &.desktop-logo {
            display: block;
        }
        &.light-logo {
            display: block;
        }
    }
    &.horizontal .logo-horizontal .header-brand-img.light-logo1 {
        display: none;
    }
    .app-header .header-right-icons .nav-link.icon {
        color: #d5d7df!important;
    }
    .app-header .header-right-icons .nav-link.icon:hover {
        color: $white;
    }
    .app-sidebar__toggle {
        color: #d5d7df!important;
    }
    .app-sidebar__toggle:after {
        background: none;
    }
    .nav-link.icon i::after {
        background: none;
    }
    &.transparent-mode .app-header.sticky-pin {
        background: $dark-theme-1;
    }
}

@media (max-width: 768px) {
    .dark-header {
        .app-header .header-brand1 .header-brand-img {
            &.light-logo1 {
                display: none !important;
                // margin-top: 5px;
            }
            &.desktop-logo {
                display: block;
            }
        }
        .hor-header {
            .header-brand-img {
                &.desktop-logo {
                    display: none;
                }
                &.light-logo {
                    margin: 0 auto;
                    margin-top: 6px;
                }
            }
        }
        .app-header.header .header-brand-img.desktop-logo{
            display: block;
        }
        .logo-horizontal .header-brand-img.light-logo1{
            display: none !important;
        }
    }
}

@media (min-width: 992px) {
    .dark-header {
        .hor-header.header .header-brand .header-brand-img {
            &.desktop-lgo {
                display: none;
            }
            &.dark-logo {
                display: block;
                // margin-top: 5px;
            }
        }
    }
}

