            }
            &.dark-logo {
                display: block !important;
                // margin-top: 5px;
            }
        }
    }
}

.light-mode.dark-header .hor-header .mobile-header.hor-mobile-header {
    background: #30304d !important;
}


/*Header-light*/

.dark-mode.header-light {
    .app-header,
    .hor-header {
        background: $white;
        border-bottom: 1px solid rgb(236, 240, 250);
    }
    .app-sidebar {
        box-shadow: none;
    }
    .animated-arrow {
        span {
            background: #5c678f;
            &:before,
            &:after {
                background: #5c678f;
            }
        }
    }
    &.active .animated-arrow span {
        background-color: transparent;
    }
    .header .form-inline .form-control {
        border: 1px solid transparent !important;
        background: #f1f5ff;
        color: $black;
    }
    .hor-header .header-brand-img.desktop-logo-1 {
        display: none;
    }
    .drop-profile a {
        color: #171a29;
    }
    .hor-header .header-brand-img.desktop-logo {
        display: block;
    }
    .header .nav-link.icon i {
        color: $dark-theme-1;
    }
    .header .nav-link.icon i {
        color: $primary-1;
    }
    .app-sidebar__toggle {
        a {
            color: #555b95;
